import Vue from 'vue'
import Router from 'vue-router'
import home from './views/home.vue'

Vue.use(Router)

export default new Router({
  linkActiveClass:'active',
  routes: [
    {
      path: '/',
      redirect: '/home'
    },
    {
      path: '/home',
      name: 'home',
      component: home
    },
    {
      path: '/game',
      name: 'game',
      component: () => import('./views/game.vue')
    },
    {
      path: '/mingyan',
      name: 'mingyan',
      component: () => import('./views/mingyan.vue')
    },
    {
      path: '/gupiao',
      name: 'gupiao',
      component: () => import('./views/gupiao.vue')
    },
    {
      path: '/city',
      name: 'city',
      component: () => import('./views/city.vue')
    }
  ]
})
